.searchInputs {
    position: absolute;
    top: 40%;
    right: 10%;
    display: flex;
  }
  
  .search input {
    background-color: black;
    color: white;
    border: 0;
    border-radius: 2px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    font-size: 18px;
    padding: 1px;
    height: 30px;
    width: 300px;
  }
  
  .searchIcon {
    height: 20px;
    width: 50px;
    background-color: black;
    display: grid;
    place-items: center;
  }
  
  input:focus {
    outline: none;
  }
  .searchIcon svg {
    font-size: 35px;
  }
  
  .dataResult {
    position: absolute;
    top: 70%;
    right: 13.7%;
    width: 300px;
    height: 200px;
    background-color: black;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
  }
  
  /* .dataResult::-webkit-scrollbar {
    display: none;
  } */
  
  .dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: white;
  }
  
  
  p {
    padding: 1rem;
    color: white;
  }
  
  p:hover {
    background-color: lightgrey;
  }
  
  #clearBtn {
    cursor: pointer;
  }